import axios from 'axios';
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { FormStyle, InvalidSpan } from './formStyle';
import StyledButton from '../../components/button/BtnForm';
import { LoginContDiv, SignupLink } from './loginStyle';

const LoginForm = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [errorMsg, setErrorMsg] = useState('');
  const URL = 'https://mandarin.api.weniv.co.kr';
  const navigate = useNavigate();

  const login = async () => {
    try {
      const res = await axios.post(`${URL}/user/login`, {
        user: { email: `${email}`, password: `${password}` },
        headers: { 'Content-Type': 'application/json' },
      });

      if (res.data.message) {
        setErrorMsg(res.data.message);
      } else {
        localStorage.setItem('accountname', res.data.user.accountname);
        localStorage.setItem('token', res.data.user.token);
        localStorage.setItem('myprofileImg', res.data.user.image);
        navigate('/home');
        console.log(res.data);
      }
    } catch (error) {
      setErrorMsg(error.response.data);
    }
  };

  return (
    <LoginContDiv>
      <h2></h2>
      <FormStyle>
        <label htmlFor="email"></label>
        <input
          id="email"
          type="email"
          required
          placeholder=" ."
          onChange={(e) => setEmail(e.target.value)}
        />

        <label htmlFor="password"></label>
        <input
          id="password"
          type="password"
          required
          placeholder=" . (6 )"
          onChange={(e) => setPassword(e.target.value)}
        />

        <InvalidSpan style={{ color: 'red' }}>{errorMsg}</InvalidSpan>
        <StyledButton
          type="submit"
          onClick={login}
          disabled={!(email && password)}
        >
          
        </StyledButton>
        <SignupLink to="/signup"> </SignupLink>
      </FormStyle>
    </LoginContDiv>
  );
};

export default LoginForm;
