import { useNavigate } from 'react-router-dom';
import { HeaderContainer, Title, BackBtn } from './headerStyle';

const HeaderFollowing = () => {
  const navigate = useNavigate();

  const handleBack = () => {
    navigate(-1);
  };
  return (
    <HeaderContainer>
      <BackBtn onClick={handleBack} />
      <Title></Title>
    </HeaderContainer>
  );
};

export default HeaderFollowing;
