import { ConfirmModal, Btnwrap } from './confirmModalStyle';

const ReportMiniModal = ({ closeModal }) => {
  const accountName = localStorage.getItem('accountname');

  return (
    <ConfirmModal>
      <h3>
        {accountName},    .
        <span>
          PORE    <br />   .
        </span>
      </h3>
      <Btnwrap
        onClick={() => {
          closeModal();
        }}
      >
        <button type="button"></button>
      </Btnwrap>
    </ConfirmModal>
  );
};

export default ReportMiniModal;
