import { useEffect, useRef, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import axios from 'axios';
import { FormStyle, InvalidSpan } from '../formStyle';
import {
  ProfileContDiv,
  ProfileImgWrap,
  ProfileImg,
  ProfileImgLabel,
  Wrapper,
} from './setProfileStyle';
import StyledButton from '../../../components/button/BtnForm';
import profileBasicImg from '../../../assets/icons/basic-profile-round.svg';

const SetProfileForm = () => {
  // ,  
  const location = useLocation();
  const navigate = useNavigate();
  const { email, password } = { ...location.state };
  console.log(`: ${email}`);
  console.log(`: ${password}`);

  // API 
  const URL = 'https://mandarin.api.weniv.co.kr';

  //  ,  , ID
  const [profileImg, setProfileImg] = useState(profileBasicImg);
  const [userName, setUserName] = useState('');
  const [userId, setUserId] = useState('');
  const [userIntro, setUserIntro] = useState('');
  const fileInput = useRef(null);

  //   
  const [userNameMsg, setUserNameMsg] = useState('');
  const [userIdMsg, setUserIdMsg] = useState('');

  //  
  const [isValidUserName, setIsValidUserName] = useState(false);
  const [isValidUserId, setIsValidUserId] = useState(false);

  //  API
  const handleSingUp = async () => {
    try {
      const response = await axios.post(`${URL}/user`, {
        user: {
          username: `${userName}`,
          email: `${email}`,
          password: `${password}`,
          accountname: `${userId}`,
          intro: `${userIntro}`,
          image: `${profileImg}`,
        },
        headers: {
          'Content-type': 'application/json',
        },
      });

      if (response) {
        navigate('/login');
      } else {
        console.log(response.data.message);
      }
    } catch (error) {
      console.log(error.response.data);
    }
  };

  //    
  const validUserName = () => {
    if (userName.length >= 10) {
      setUserNameMsg('10  .');
      setIsValidUserName(false);
    } else if (userName.length <= 1) {
      setUserNameMsg('2  .');
      setIsValidUserName(false);
    } else {
      setUserNameMsg('  .');
      setIsValidUserName(true);
    }
  };

  // ID 
  const userIdRegex = /^[a-zA-Z0-9_.]{2,10}$/;

  // ID  
  const validUserId = async () => {
    try {
      const response = await axios.post(`${URL}/user/accountnamevalid`, {
        user: {
          accountname: `${userId}`,
        },
        headers: {
          'Content-type': 'application/json',
        },
      });

      if (response && userIdRegex.test(userId)) {
        if (response.data.message === '  ID .') {
          setUserIdMsg(response.data.message);
          setIsValidUserId(false);
        } else {
          setUserIdMsg(response.data.message);
          setIsValidUserId(true);
        }
      } else {
        setUserIdMsg('ID  .');
        setIsValidUserId(false);
      }
    } catch (error) {
      console.log(error.response.data);
    }
  };

  useEffect(() => {
    validUserName();
    validUserId();
  }, [userName, userId]);

  const checkInput = (e) => {
    if (e.target.id === 'userName') {
      setUserName(e.target.value);
    } else if (e.target.id === 'userId') {
      setUserId(e.target.value);
    } else if (e.target.id === 'userIntro') {
      setUserIntro(e.target.value);
    }
  };

  //  API
  const profileChange = async () => {
    try {
      const file = fileInput.current.files[0];
      const formData = new FormData();

      formData.append('image', file);

      const res = await axios.post(`${URL}/image/uploadfile`, formData);
      const fileName = res.data.filename;

      setProfileImg(`${URL}/${fileName}`);
    } catch (error) {
      console.log('');
    }
  };

  return (
    <ProfileContDiv>
      <FormStyle>
        <h2> </h2>
        <p>    .</p>
        <Wrapper>
          <ProfileImgWrap>
            <ProfileImg src={profileImg} alt=" " />
          </ProfileImgWrap>
          <ProfileImgLabel className="set-profile" htmlFor="profileImg" />
        </Wrapper>
        <input
          id="profileImg"
          type="file"
          style={{ display: 'none' }}
          accept="image/jpg,image/png,image/jpeg"
          name="profile_img"
          onChange={profileChange}
          ref={fileInput}
        />
        <label htmlFor="userName"> </label>
        <input
          id="userName"
          type="text"
          placeholder="2~10  ."
          onChange={checkInput}
          required
        />
        {userName.length > 0 && (
          <InvalidSpan className={`${isValidUserName ? 'success' : 'error'}`}>
            {userNameMsg}
          </InvalidSpan>
        )}

        {/*    ex)@heesu.u */}
        <label htmlFor="userId"> ID</label>
        <input
          id="userId"
          type="text"
          placeholder="10  , , (.),(_)  ."
          onChange={checkInput}
          required
        />
        {userId.length > 0 && (
          <InvalidSpan className={`${isValidUserId ? 'success' : 'error'}`}>
            {userIdMsg}
          </InvalidSpan>
        )}

        <label htmlFor="userIntro"></label>
        <input
          id="userIntro"
          type="text"
          placeholder="   !"
          onChange={checkInput}
        />
        <StyledButton
          type="submit"
          disabled={!(isValidUserName && isValidUserId)}
          onClick={handleSingUp}
        >
          PORE 
        </StyledButton>
      </FormStyle>
    </ProfileContDiv>
  );
};

export default SetProfileForm;
