import { useEffect, useState, useRef } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import { FormStyle, InvalidSpan } from '../../Login/formStyle';
import StyledButton from '../../../components/button/BtnForm';
import { EditProfileContDiv } from './editProfileStyle';
import {
  ProfileImgWrap,
  ProfileImg,
  ProfileImgLabel,
  Wrapper,
} from '../../Login/SetProfile/setProfileStyle';

const EditProfileForm = () => {
  //
  const URL = 'https://mandarin.api.weniv.co.kr';
  const authToken = localStorage.getItem('token');
  const [username, setUsername] = useState('');
  const [accountname, setAccountname] = useState('');
  const [intro, setIntro] = useState('');
  const [image, setImage] = useState('');
  const fileInput = useRef(null);
  const [userNameMsg, setUserNameMsg] = useState('');
  const [isValidUserName, setIsValidUserName] = useState(false);
  const navigate = useNavigate();

  //   
  const getProfile = async () => {
    try {
      const res = await axios.get(`${URL}/user/myinfo`, {
        headers: { Authorization: `Bearer ${authToken}` },
      });
      console.log(res.data.user);
      setUsername(res.data.user.username);
      setAccountname(res.data.user.accountname);
      setIntro(res.data.user.intro);
      setImage(res.data.user.image);
    } catch (error) {
      console.log('');
    }
  };
  useEffect(() => getProfile, []);

  //   ()
  const profileChange = async () => {
    try {
      const file = fileInput.current.files[0];
      const formData = new FormData();

      formData.append('image', file);

      const res = await axios.post(`${URL}/image/uploadfile`, formData);
      const fileName = res.data.filename;

      setImage(`${URL}/${fileName}`);
    } catch (error) {
      console.log('');
    }
  };

  //    
  const validUserName = () => {
    if (username.length >= 10) {
      setUserNameMsg('10  .');
      setIsValidUserName(false);
    } else if (username.length <= 1) {
      setUserNameMsg('2  .');
      setIsValidUserName(false);
    } else {
      setUserNameMsg('  .');
      setIsValidUserName(true);
    }
  };
  useEffect(() => {
    validUserName();
  }, [username]);

  //  
  const body = {
    user: {
      username: `${username}`,
      accountname: `${accountname}`,
      intro: `${intro}`,
      image: `${image}`,
    },
  };
  const editProfile = async () => {
    try {
      const res = await axios.put(`${URL}/user`, JSON.stringify(body), {
        headers: {
          Authorization: `Bearer ${authToken}`,
          'Content-type': 'application/json',
        },
      });
      console.log(res.data);
      navigate('/profile');
    } catch (error) {
      console.log('');
    }
  };

  return (
    <EditProfileContDiv>
      <h2> </h2>
      <Wrapper>
        <ProfileImgWrap>
          <ProfileImg src={image} alt=" " />
        </ProfileImgWrap>
        <ProfileImgLabel htmlFor="profileImg" />
      </Wrapper>
      <input
        id="profileImg"
        type="file"
        style={{ display: 'none' }}
        accept="image/jpg,image/png,image/jpeg"
        name="profile_img"
        onChange={profileChange}
        ref={fileInput}
      />
      <FormStyle>
        <label htmlFor="userName"> </label>
        <input
          id="userName"
          type="text"
          required
          placeholder="2~10  ."
          value={username}
          onChange={(e) => {
            setUsername(e.target.value);
          }}
        />
        {username.length > 0 && (
          <InvalidSpan className={`${isValidUserName ? 'success' : 'error'}`}>
            {userNameMsg}
          </InvalidSpan>
        )}
        <label htmlFor="userId"> ID</label>
        <input
          id="userId"
          type="text"
          required
          placeholder=", , (.),(_)  ."
          value={accountname}
        />
        <label htmlFor="userDesc"></label>
        <input
          id="userDesc"
          type="text"
          required
          placeholder="   !"
          value={intro}
          onChange={(e) => {
            setIntro(e.target.value);
          }}
        />
        <StyledButton
          type="submit"
          disabled={!isValidUserName}
          onClick={editProfile}
        >
          
        </StyledButton>
      </FormStyle>
    </EditProfileContDiv>
  );
};

export default EditProfileForm;
