import { useCallback, useRef, useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import BtnUpload from '../../../components/button/BtnUpload';
import { Input, Form, TextInput } from '../../../components/input/Input';
import HeaderB from '../../../components/header/HeaderB';
import {
  PreviewImg,
  SubmitImg,
  Button,
  PhotoWrap,
} from './uploadPhotoZoneStyle';

const UploadPhotoZone = () => {
  const [imgFile, setImgFile] = useState('');
  const [address, setAddress] = useState('');
  const [date, setDate] = useState('');
  const [desc, setDesc] = useState('');
  const imgRef = useRef();
  const textRef = useRef();
  const handleResizeHeight = useCallback(() => {
    textRef.current.style.height = '1px';
    textRef.current.style.height = `${textRef.current.scrollHeight}px`;
  }, []);
  const navigate = useNavigate();
  const URL = 'https://mandarin.api.weniv.co.kr';

  const uploadPhoto = async () => {
    try {
      const authToken = localStorage.getItem('token');
      const body = {
        product: {
          itemName: address,
          price: Number(date),
          link: desc,
          itemImage: imgFile,
        },
      };
      const res = await axios.post(`${URL}/product`, JSON.stringify(body), {
        headers: {
          Authorization: `Bearer ${authToken}`,
          'Content-type': 'application/json',
        },
      });
      console.log(res);
      navigate(`/profile`);
    } catch (error) {
      console.log('');
    }
  };

  const imgApi = async () => {
    try {
      const file = imgRef.current.files[0];
      const formData = new FormData();
      formData.append('image', file);
      const res = await axios.post(`${URL}/image/uploadfile`, formData);
      const fileName = res.data.filename;
      setImgFile(`${URL}/${fileName}`);
    } catch (error) {
      console.log('');
    }
  };
  return (
    <>
      <HeaderB />
      <Form action="submit">
        <label htmlFor="photo"> </label>
        <PhotoWrap>
          {imgFile ? null : <SubmitImg htmlFor="photo" />}
          <PreviewImg src={imgFile || null} alt="" />
          {imgFile ? (
            <Button
              onClick={() => {
                setImgFile('');
              }}
            />
          ) : null}
          <Input
            id="photo"
            type="file"
            style={{ display: 'none' }}
            onChange={() => {
              imgApi();
            }}
            ref={imgRef}
          />
        </PhotoWrap>
        <label htmlFor="address"> </label>
        <Input
          id="address"
          type="text"
          required
          onChange={(e) => setAddress(e.target.value)}
          placeholder="  ."
        />
        <label htmlFor="date"> </label>
        <Input
          id="date"
          type="number"
          required
          onChange={(e) => setDate(e.target.value)}
          placeholder="  . )20221212"
        />
        <label htmlFor="cont"></label>
        <TextInput
          id="cont"
          type="text"
          required
          onChange={(e) => setDesc(e.target.value)}
          placeholder="    ."
          ref={textRef}
          onInput={handleResizeHeight}
          rows="1"
        />
        <BtnUpload type="button" onClick={uploadPhoto}>
          
        </BtnUpload>
      </Form>
    </>
  );
};

export default UploadPhotoZone;
